import { Component, Prop, State, Event, EventEmitter, h, AttachInternals } from '@stencil/core';
import { syncCheckableFormValue, validateRequired } from '../../utils/form';

export type ToggleSize = 'sm' | 'md';

@Component({
  tag: 'bh-toggle',
  styleUrl: 'bh-toggle.css',
  shadow: true,
  formAssociated: true,
})
export class BhToggle {
  @AttachInternals() internals!: ElementInternals;
  /**
   * The size of the toggle
   */
  @Prop() size: ToggleSize = 'md';

  /**
   * Whether to show the label and supporting text
   */
  @Prop() showText: boolean = false;

  /**
   * The label text
   */
  @Prop() label: string = '';

  /**
   * Supporting text below the label
   */
  @Prop() supportingText: string = '';

  /**
   * Whether the toggle is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Whether the toggle is checked (controlled mode)
   */
  @Prop({ mutable: true }) checked: boolean = false;

  /**
   * Name attribute for form submission
   */
  @Prop() name: string = '';

  /**
   * Value when checked (default: 'on')
   */
  @Prop() value: string = 'on';

  /**
   * Whether the toggle is required
   */
  @Prop() required: boolean = false;

  /**
   * Default checked state for form reset
   */
  @Prop() defaultChecked: boolean = false;

  /**
   * Internal focus state
   */
  @State() isFocused: boolean = false;

  /**
   * Internal hover state
   */
  @State() isHovered: boolean = false;

  /**
   * Emitted when the toggle state changes
   */
  @Event() bhChange!: EventEmitter<boolean>;

  componentDidLoad() {
    syncCheckableFormValue(this.internals, this.name, this.checked, this.value);
    validateRequired(this.internals, this.required, this.checked);
  }

  formResetCallback() {
    this.checked = this.defaultChecked;
    syncCheckableFormValue(this.internals, this.name, this.checked, this.value);
    validateRequired(this.internals, this.required, this.checked);
  }

  private handleToggle = (): void => {
    if (this.disabled) return;

    this.checked = !this.checked;
    syncCheckableFormValue(this.internals, this.name, this.checked, this.value);
    validateRequired(this.internals, this.required, this.checked);
    this.bhChange.emit(this.checked);
  };

  private handleKeyDown = (event: KeyboardEvent): void => {
    if (event.key === ' ' || event.key === 'Enter') {
      event.preventDefault();
      this.handleToggle();
    }
  };

  private handleMouseEnter = (): void => {
    this.isHovered = true;
  };

  private handleMouseLeave = (): void => {
    this.isHovered = false;
  };

  private handleFocus = (): void => {
    this.isFocused = true;
  };

  private handleBlur = (): void => {
    this.isFocused = false;
  };

  render() {
    const wrapperClasses = {
      'toggle-wrapper': true,
      'toggle-with-text': this.showText,
      [`toggle-size-${this.size}`]: true,
    };

    const switchClasses = {
      'toggle-switch': true,
      'toggle-pressed': this.checked,
      'toggle-unpressed': !this.checked,
      [`toggle-${this.size}`]: true,
      'toggle-disabled': this.disabled,
      'toggle-focus': this.isFocused && !this.disabled,
      'toggle-hover': this.isHovered && !this.disabled,
    };

    const buttonClasses = {
      'toggle-button': true,
      'toggle-button-pressed': this.checked,
      'toggle-button-unpressed': !this.checked,
      [`toggle-button-${this.size}`]: true,
      'toggle-button-disabled': this.disabled,
    };

    const labelClasses = {
      'toggle-label': true,
      [`toggle-label-${this.size}`]: true,
    };

    return (
      <div class={wrapperClasses} part="wrapper">
        <div
          class={switchClasses}
          onClick={this.handleToggle}
          onKeyDown={this.handleKeyDown}
          onMouseEnter={this.handleMouseEnter}
          onMouseLeave={this.handleMouseLeave}
          onFocus={this.handleFocus}
          onBlur={this.handleBlur}
          tabIndex={this.disabled ? -1 : 0}
          role="switch"
          aria-checked={this.checked ? 'true' : 'false'}
          aria-disabled={this.disabled ? 'true' : 'false'}
          aria-label={this.label || undefined}
          part="switch"
        >
          <div class={buttonClasses} part="button"></div>
        </div>

        {this.showText && (this.label || this.supportingText) && (
          <div class="toggle-text">
            {this.label && (
              <span class={labelClasses}>{this.label}</span>
            )}
            {this.supportingText && (
              <div class="toggle-supporting-text">{this.supportingText}</div>
            )}
          </div>
        )}
      </div>
    );
  }
}
